* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    min-height: 100vh;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.App {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: auto;
    border: 1px solid mediumblue;
}

header {
    width: 100%;
    background-color: #8eecf5;
    padding: 0 0.25rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

main {
    margin-top: 2rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    align-items: center;
    overflow-y: auto;
    background-color: #fbf8cc;
}

fieldset {
    min-width: 40%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 0;
}

#question {
    font-size: 1.7rem;
    font-family:din-round,sans-serif;
    font-weight: 700;
    margin-bottom: 3rem;
}

.choices {
    margin-top: 2rem;
    display: flex;
    width: 70%;
    max-height: 30%;
    justify-content: space-around;
}

.choice {
    font-size: 1.4rem;
    font-weight: 400;
    max-width: 30%;
    max-height: 20%;
    border: 2px solid  #fde4cf;
    border-radius: 3%;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: space-around;
}

img {
    max-height: 70%;
    width: 100%;
    border-bottom: 2px solid  #fde4cf;
    background: local;
}


.choice input[type="radio"] {
    opacity: 0;
    position: fixed;
    width: 0;
}

